import $ from 'jquery';


import bindShowNav from './bind-show-nav';
import loadButtons from './buttons';
import loadCheckboxes from './checkboxes';


window.$ = $;


$(document).ready(() => {
  bindShowNav($);
  loadButtons($);
  loadCheckboxes($);

  $('#rescrape-slack').on('click', (e) => {
    e.preventDefault();

    $('.slack-loading').addClass('in-progress');
    $('.slack-loading').addClass('shown');

    const dataURL = $(e.currentTarget).attr('data-url');

    setTimeout(() => {
      const rescrape = $.ajax({ dataType: 'json', url: dataURL });

      rescrape.done((data) => {
        if (
          (typeof data.msg !== 'undefined') &&
          (data.msg.toLowerCase() === 'rescrape ok')
        ) {
          $('.slack-loading').removeClass('in-progress');
          $('.slack-loading').addClass('success');
          setTimeout(() => {
            $('.message[message-type="success"]').find('.big-icon')
                .addClass('animated');
          }, 83);

          setTimeout(() => { location.reload(); }, 2400);
        } else {
          $('.slack-loading').removeClass('in-progress');
          $('.slack-loading').addClass('error');

          setTimeout(() => { $('.slack-loading').removeClass('shown'); }, 2400);
        }
      });

      rescrape.fail((resp, textStatus, errorThrown) => {
        console.error(resp, textStatus, errorThrown);
        $('.slack-loading').addClass('error');
      });
    }, 2000);
  });
});
